@import url(https://fonts.googleapis.com/css?family=Kreon|Quicksand);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #3a3a3a;
}

.Quicksand-Text {
  font-family: "Quicksand", sans-serif !important;
}

.Kreon-Text {
  font-family: "Kreon", serif !important;
}

.Glow {
  font-size: 4.5em;
  color: #00b6ff;
  -webkit-animation: glow 1s ease-in-out infinite alternate;
  animation: glow 1s ease-in-out infinite alternate;
}

@-webkit-keyframes glow {
  from {
    text-shadow: 0 0 0.05em #ffddad, 0 0 0.1em #ffddad, 0 0 0.15em #7cd9ff,
      0 0 0.35em #7cd9ff, 0 0 0.4em #7cd9ff, 0 0 0.55em #7cd9ff,
      0 0 0.7em #7cd9ff;
  }
  to {
    text-shadow: 0 0 0.15em #fff, 0 0 0.25em #ffddad, 0 0 0.35em #5bd0ff,
      0 0 0.55em #5bd0ff, 0 0 0.65em #5bd0ff, 0 0 0.75em #5bd0ff,
      0 0 0.85em #5bd0ff;
  }
}

@keyframes glow {
  from {
    text-shadow: 0 0 0.05em #ffddad, 0 0 0.1em #ffddad, 0 0 0.15em #7cd9ff,
      0 0 0.35em #7cd9ff, 0 0 0.4em #7cd9ff, 0 0 0.55em #7cd9ff,
      0 0 0.7em #7cd9ff;
  }
  to {
    text-shadow: 0 0 0.15em #fff, 0 0 0.25em #ffddad, 0 0 0.35em #5bd0ff,
      0 0 0.55em #5bd0ff, 0 0 0.65em #5bd0ff, 0 0 0.75em #5bd0ff,
      0 0 0.85em #5bd0ff;
  }
}

footer {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 4em;
}

.navbar {
  margin-bottom: 20px;
}

